import React, { Component } from 'react';
import { Table, Button } from 'antd';
import { setStudentPreparingGraduation } from 'dump/data';

class TableOwedExamGraduation extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dayOfBirth',
        key: 'dayOfBirth',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'TBC tích lũy',
        dataIndex: 'cumulativeAverage',
        key: 'cumulativeAverage',
      },

      {
        title: 'Xếp hạng',
        dataIndex: 'rankGraduation',
        key: 'rankGraduation',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: () => (
          <Button type="primary" onClick={this.showModal}>
            Xem
          </Button>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setStudentPreparingGraduation}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableOwedExamGraduation;



