import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import FormInfoStudentPreparingGraduation from './FormInfoStudentPreparingGraduation';
import MenuTabTop from './MenuTabTop';
import './style.scss';

class ListStudentPreparingToGraduationPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Xét tốt nghiệp ra trường">
        <Row className="rowMargin">
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoStudentPreparingGraduation />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListStudentPreparingToGraduationPage />
  </Page>
);



