import React, { Component } from 'react';
import { Row, Col, Form, Tabs, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import TableQualifiedDoThesis from './TableQualifiedDoThesis';
import TableQualifiedExamGraduation from './TableQualifiedExamGraduation';
import TableOwedExamGraduation from './TableOwedExamGraduation';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoStudentPreparingGraduation extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { TabPane } = Tabs;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Điều kiệnTBCHT tích lũy >=:">
              {getFieldDecorator('schoolYearsTime', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Số tín chỉ tối thiểu đạt >=:">
              {getFieldDecorator('schoolYearsTime', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Số tín chỉ thi lại <=:">
              {getFieldDecorator('schoolYearsTime', {})(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Tabs defaultActiveKey="1">
              <TabPane tab="Luận văn" key="1">
                <TableQualifiedDoThesis />
              </TabPane>
              <TabPane tab="Thi tốt nghiệp" key="2">
                <TableQualifiedExamGraduation />
              </TabPane>
              <TabPane tab="Nợ thi tốt nghiệp" key="3">
                <TableOwedExamGraduation />
              </TabPane>
            </Tabs>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoStudentPreparingGraduation;



