import React, { Component } from 'react';
import { Row, Col, Menu, Icon } from 'antd';
import './style.scss';

class MenuTabTop extends Component {
  state = {
    current: 'printTranscript',
  };
  handleClick = e => {
    console.log('click ', e);
    this.setState({
      current: e.key,
    });
  };
  render() {
    return (
      <Row>
        <Col span={24}>
          <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
            <Menu.Item key="considerAuto">
              <Icon type="setting" /> Xét tự động
            </Menu.Item>
            <Menu.Item key="forwardToExamGraduate">
              <Icon type="right-circle" theme="twoTone" twoToneColor="#00cc00" /> Chuyển thi tốt
              nghiệp
            </Menu.Item>
            <Menu.Item key="printReport">
              <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" /> In báo cáo
            </Menu.Item>
            <Menu.Item key="fileExcel">
              <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" /> Xuất Excel
            </Menu.Item>
          </Menu>
        </Col>
      </Row>
    );
  }
}

export default MenuTabTop;



